export function prepareList(items, defaultData = {}, uidField = 'uid') {
    return items.map(item => prepareListItem(item, defaultData || {}, uidField));
}
export function prepareListItem(item, defaultData = {}, uidField = 'uid') {
    return {
        ...{ [uidField]: u.uid() },
        ...defaultData,
        ...item,
    };
}
